import LoadImg from '@/components/LoadImg.vue'
export default {
  name: "user-add",
  components: {
    LoadImg
  },
  data () {
    return {
      imgSrc: ''
    }
  },
  created(){
    this.imgSrc = 'http://mobile.qq.com/qrcode?url='+encodeURIComponent(this.$config.VJ+this.$barId)
  },
  beforeRouteLeave (to, from, next) {
    console.log('beforeRouteLeave',to,from)
    if (to.name === 'BroadcastUser'){
      to.meta.isBack = true
    }
    next()
  }
}



